@import "./global.scss";

.app {
  height: 100vh;

  .sections {
    width: 100%;
    height: 100vh;
    background-color: red;
    position: relative;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;
    &::-webkit-scrollbar{
      display: none;
    }

    > * {
      width: 100vw;
      height: 100vh;
      scroll-snap-align: start;
    }
  }
}